import * as fetch from 'isomorphic-fetch'
import * as crypto from 'crypto'
import zip = require('lodash.zipobject');
import * as path from "path";
import {HttpError} from "../Error/HttpError";
import {Api} from "../Account/Api";
import {iBinanceRestOpts} from "./Interfaces/iRestOptions";
import {iBinanceOptions} from "@michael-mcanulty/bb-models";
import {ServerTime} from "./Interfaces/ServerTime";
import {eMethod} from "./eMethod";
import {iCallOpts} from "./Interfaces/iCallOpts";
import {publicDecrypt} from "crypto";

export class Rest{
	public apiAuth: Api;
	public options:iBinanceOptions;
	public base:string = 'https://api.binance.com';
	private checkParams(name: string, payload: object, requires: any[]): boolean {
		if (!payload) {
			throw new Error('You need to pass a payload object.')
		}

		requires.forEach(r => {
			if (!payload[r] && isNaN(payload[r])) {
				throw new Error(`Method ${name} requires ${r} parameter.`)
			}
		});

		return true
	}
	private makeQueryString(params:any):string{
		let result:string;
		if(!params){
			result = "";
		}else{
			const esc = encodeURIComponent;
			result = Object.keys(params).map(k => `?${esc(k)}=${esc(params[k])}`).join('&');
		}
		return result;
	}
	private buildUrl(path:string, data:object, noData:boolean = false):string{
		return `${this.base}${path.includes('/wapi') ? '' : '/api'}${path}${noData ? '': this.makeQueryString(data)}`;
	}

	public fetch(path:string, payload:any, callOptions: iCallOpts):Promise<any>{
		return new Promise(async (resolve, reject)=>{
			let json:any;
			let msg:string;
			let err:HttpError;
			let params:string = this.buildUrl(path, payload, callOptions.noData);
			let reqOpts: RequestInit = <RequestInit>{};
			reqOpts.method = eMethod[eMethod.GET];
			reqOpts.headers = {};
			let res:Response = await fetch(params, reqOpts);
			json = await res.json();
			if (!res.ok) {
				msg = json.msg || `${res.status} ${res.statusText}`;
				err = new HttpError(msg, json.code);
				reject(err);
			}else{
				resolve(json);
			}
		})
	}

	time():Promise<boolean>{
		return new Promise(async (resolve, reject) => {
			try{
				let pingRes:boolean = await this.publicCall('/v1/ping');
				resolve(pingRes);
			}catch(err){
				reject(`Error in server time sync. Message: ${err}`);
			}
		});
	}
	ping():Promise<number>{
		return new Promise(async (resolve, reject) => {

			try{
				let time:number;
				let server:ServerTime = await this.publicCall('/v1/time');
				time = server.serverTime;
				resolve(time);
			}catch(err){
				reject(`Error in server time sync. Message: ${err}`);
			}
		});
	}
	publicCall(path:string, data?: any, callOptions?: iCallOpts):Promise<any>{
		return new Promise(async (resolve, reject) => {
			let result:any;
		  try{
				result = await this.fetch(path, data, callOptions);
				resolve(result);
			}catch(err){
		  	reject(err);
			}
		});
	}

	public privateCall(path:string, data: any, callOptions: iCallOpts):Promise<any> {
		return new Promise(async (resolve, reject) => {
			let result: any;
			let signature:string;
			let timestamp: ServerTime;
			let newData: any;
			let headersInit:HeadersInit = {'X-MBX-APIKEY': this.apiAuth.key};
			let headers:Headers = new Headers(headersInit);
			let fetchPath:string = this.buildUrl(path, data);
			callOptions.headers = headers;
			callOptions.method = eMethod.GET;
			callOptions.json = true;

			if (!this.apiAuth.key || !this.apiAuth.secret) {
				throw new Error('You need to pass an API key and secret to make authenticated calls.');
			}

			try {
				if (data && this.options.useServerTime) {
					timestamp = await this.publicCall('/v1/time');
				} else {
					timestamp.serverTime = Date.now();
				}

				signature = crypto.createHmac('sha256', this.apiAuth.secret).update(this.makeQueryString({
					...data,
					timestamp
				}).substr(1)).digest('hex');
				newData = callOptions.noExtra ? data : {...data, timestamp, signature};

				result = await this.fetch(fetchPath, newData, callOptions);
				resolve(result);
			}catch(err){
				reject(err);
			}
		});
	}


	constructor(spiAuth: Api){
		this.apiAuth = spiAuth;
	}
}
